body{
  margin: 0;
  padding: 0;
  background-color: rgb(185, 185, 185);
  font-family: 'Roboto', sans-serif;
}

.App {
  text-align: center;
  width: 100%;
  max-width: 600px;
  height: 420px;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.75);
  color: white;
  margin-top: 50px;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1.5fr 1fr;
  position: relative;
  background-size: contain;
}

.App .box-main {
  display: grid;
  grid-column: 1/2;
  background-color: rgba(0, 0, 0, 0.7);
}

.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  -webkit-transition: all 350ms ease-in-out;
  transition: all 350ms ease-in-out;
}

.Days {
    background-color: rgba(255, 255, 255, 0.767);
    height: 100%;
    width: 250px;
    grid-column: 2 / 3;
    grid-row: 1/3;
    display: grid;
    grid-template-rows: 40px;
}

.Days .box {
    height: 330px; 
    margin: auto;
    display: grid;
    width: 100%;
    overflow-y: scroll;
    grid-row-gap: 15px;
}

@media (max-width: 500px) {
    .Days {
        width: 200px;
    }
}
.Day{
    color: #000000;
    border-bottom: 1px solid rgba(0, 0, 0, 0.548);
    width: 100%;
    max-width: 175px;
    margin: auto;
    height: 50px;
    display: grid;
    grid-template-rows: 1fr 2fr;
    grid-template-columns: repeat(2, 1fr);
    text-align: left;
}

.Day .degree {
    grid-row: 2/3;
    font-size: 23px;
    margin-left: 10px;
}

.Day .weather {
    margin-left: 12px;
    font-size: 12px;
}

.Day .date {
    grid-row: 2/3;
    font-size: 11px;
    text-align: right;
    margin-top: auto;
    margin-bottom: 5px;
    margin-right: 5px;
}

.Day .deg-symbol {
    font-size: 15px;
    position: relative;
    top: -8px;
}
.Head {
    height: 60px;
    font-size: 12px;
    text-align: left;
    margin-left: 35px;
    margin-top: 30px;
}

.Head .weather {
    font-size: 30px;
    margin-top: 10px;
}

@media (max-width: 500px) {
    .Head {
        margin-left: 15px;
    }
}
.Degree {
    font-size: 102px;
    text-align: left;
    margin-left: 35px;
    margin-top: auto;
    margin-bottom: 50px;
}

.Degree .city {
    font-size: 13px;
}

.Degree .deg-symbol {
    font-size: 60px;
    position: relative;
    top: -40px;
}

@media (max-width: 500px) {
    .Degree {
        font-size: 50px;
        text-align: left;
        margin: auto;
    }

    .Degree .deg-symbol {
        font-size: 40px;
        position: relative;
        top: -20px;
    }
}
.windows8 {
	position: relative;
	width: 78px;
	height:78px;
	margin:auto;
}

.LoadAnim {
    width: 100%;
    height: 100%;
    background-color: rgb(48, 48, 48);
    position: absolute;
    z-index: 99;
	display: grid;
	background-size: contain;
}

.windows8 .wBall {
	position: absolute;
	width: 74px;
	height: 74px;
	opacity: 0;
	-webkit-transform: rotate(225deg);
	        transform: rotate(225deg);
	-webkit-animation: orbit 3.3225s infinite;
	        animation: orbit 3.3225s infinite;
}

.windows8 .wBall .wInnerBall{
	position: absolute;
	width: 10px;
	height: 10px;
	background: rgb(255, 255, 255);
	left:0px;
	top:0px;
	border-radius: 10px;
}

.windows8 #wBall_1 {
	-webkit-animation-delay: 0.726s;
	        animation-delay: 0.726s;
}

.windows8 #wBall_2 {
	-webkit-animation-delay: 0.143s;
	        animation-delay: 0.143s;
}

.windows8 #wBall_3 {
	-webkit-animation-delay: 0.2865s;
	        animation-delay: 0.2865s;
}

.windows8 #wBall_4 {
	-webkit-animation-delay: 0.4295s;
	        animation-delay: 0.4295s;
}

.windows8 #wBall_5 {
	-webkit-animation-delay: 0.583s;
	        animation-delay: 0.583s;
}



@-webkit-keyframes orbit {
	0% {
		opacity: 1;
		z-index:99;
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
		-webkit-animation-timing-function: ease-out;
		        animation-timing-function: ease-out;
	}

	7% {
		opacity: 1;
		-webkit-transform: rotate(300deg);
		        transform: rotate(300deg);
		-webkit-animation-timing-function: linear;
		        animation-timing-function: linear;
		origin:0%;
	}

	30% {
		opacity: 1;
		-webkit-transform:rotate(410deg);
		        transform:rotate(410deg);
		-webkit-animation-timing-function: ease-in-out;
		        animation-timing-function: ease-in-out;
		origin:7%;
	}

	39% {
		opacity: 1;
		-webkit-transform: rotate(645deg);
		        transform: rotate(645deg);
		-webkit-animation-timing-function: linear;
		        animation-timing-function: linear;
		origin:30%;
	}

	70% {
		opacity: 1;
		-webkit-transform: rotate(770deg);
		        transform: rotate(770deg);
		-webkit-animation-timing-function: ease-out;
		        animation-timing-function: ease-out;
		origin:39%;
	}

	75% {
		opacity: 1;
		-webkit-transform: rotate(900deg);
		        transform: rotate(900deg);
		-webkit-animation-timing-function: ease-out;
		        animation-timing-function: ease-out;
		origin:70%;
	}

	76% {
	opacity: 0;
		-webkit-transform:rotate(900deg);
		        transform:rotate(900deg);
	}

	100% {
	opacity: 0;
		-webkit-transform: rotate(900deg);
		        transform: rotate(900deg);
	}
}



